import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {AjaxServiceService} from '../../services/ajax-service.service';

@Component({
  selector: 'app-customer-list',
  templateUrl: './customer-list.component.html',
  styleUrls: ['./customer-list.component.css']
})
export class CustomerListComponent implements OnInit {

  customerList:any[];
   p: number = 1;
   constructor(private ajaxServiceService :AjaxServiceService ,public router:Router) { }

  ngOnInit() {

    console.log("loading customer List");
    this.ajaxServiceService.getServiceForList("getAllCustomers").subscribe((customerList)=>{
     this.customerList=customerList;
      console.log("customerList "+customerList); 
    });;
    
  }

}



