import { BrowserModule } from '@angular/platform-browser';
import { NgModule, OnInit } from '@angular/core';
import { HttpModule } from '@angular/http'
import { FormsModule, ReactiveFormsModule } from '@angular/forms'
import { HttpClient } from '@angular/common/http';

import { AppComponent } from './app.component';
import { LoginComponent } from './components/login/login.component';
import { LoginService } from './services/login.service';
import { HomeComponent } from './Components/home/home.component';
import { RouterModule, Routes, Router } from '@angular/router';
import { AddCustomersComponent } from './Components/add-customers/add-customers.component';
import { CustomerListComponent } from './Components/customer-list/customer-list.component';

import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { InterceptorService } from './services/interceptor.service';
import { AjaxServiceService } from './services/ajax-service.service';
import { AuthService } from './services/auth-service.service';
import { HttpClientModule } from '@angular/common/http';
import decode from 'jwt-decode';
import {NgxPaginationModule} from 'ngx-pagination';


const appRoutes: Routes = [
  {
    path: 'Home',
    component: HomeComponent,
    children: [
      {
        path: 'CustomersList',
        component: CustomerListComponent
      },
      {
        path: 'AddCustomers',
        component: AddCustomersComponent
      }
    ]
  },
  {
    path: 'Login',
    component: LoginComponent
  }
]

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HomeComponent,
    AddCustomersComponent,
    CustomerListComponent
    
  ],
  imports: [
    BrowserModule, FormsModule, HttpModule, RouterModule.forRoot(appRoutes), 
    ReactiveFormsModule, HttpClientModule,NgxPaginationModule
  ],
  providers: [LoginService, AjaxServiceService, HttpClient,
    AuthService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: InterceptorService,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule implements OnInit {

  constructor(public router: Router) {
    this.router.navigate(['Login']);
  }

  ngOnInit() { }

}



