import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { CustomerModule} from "../../modules/customer/customer.module";
import { AddressModule } from "../../modules/address/address.module";
import {FormGroup,Validators,FormBuilder} from '@angular/forms';
import {AjaxServiceService} from '../../services/ajax-service.service';




@Component({
  selector: 'app-add-customers',
  templateUrl: './add-customers.component.html',
  styleUrls: ['./add-customers.component.css']
})
export class AddCustomersComponent implements OnInit {

  customer:any;
  addCustomerForm;

  constructor(private ajaxServiceService :AjaxServiceService,private formBuilder :FormBuilder) { }

  ngOnInit() {
    this.customer=new CustomerModule();
    this.customer.address=new AddressModule();
    this.addCustomerForm=this.formBuilder.group({
      'userName':[null,Validators.required],
      'firstName':[null,Validators.required],
      'lastName':[null,Validators.required],
      'mobileNumber':[null,Validators.required],
      'email':[null,Validators.required],
      'addressLine1':[null,Validators.required],
      'addressLine2':[null,Validators.required],
      'city':[null,Validators.required],
      'pincode':[null,Validators.required]
    });

  }

  onSubmit() {
  if(this.addCustomerForm.valid){
    console.log("submiting the customer details");
    console.log(this.customer);
    this.ajaxServiceService.postService("saveCustomer",this.customer).subscribe((customer) => {
      this.customer=customer;
    });
  }else{
    window.alert("Fill the form!!");
  }
    
  }

}



